import "./HomeScreen.css";
import ScrapeContext from "../../Services/ScrapeContext";
import useStorage from "../../Services/StorageHook";
import useSize from "../../Services/SizeHook";
import { useHistory } from "react-router-dom";
import Navbar from "./../../Components/NavBar/NavBar";
import SeriesCard from "./../../Components/SeriesCard/SeriesCard";
import Loading from "./../../Components/Loading/Loading";
import InfoCard from "./../../Components/InfoCard/InfoCard";
import Heading from "./../../Components/Heading/Heading";

function HomeScreen() {
  const storageHook = useStorage();
  const sizeHook = useSize();

  const message = {
    title: "Welcome to Weeb.eu.org",
    body: "Watch 1000+ Anime Show For Free On Weeb.eu.org",
  };

  return (
    <ScrapeContext.Consumer>
      {(state) => {
        return (
          <div ref={sizeHook.ref} className="home">
            <div className="homeNavContainer">
              <Navbar />
            </div>

            <div className="homeMain">
              
              <InfoCard
                title={message.title}
                desc={message.body}
                btnText={""}
                margin="0rem 0rem 1rem 0rem"
                onClick={() => {}}
              />

              <Recently
                list={storageHook.getRecents}
                scrapeSeries={state.scrapeSeries}
              />

              <Latest
                list={state.values?.latest}
                scrapeSeries={state.scrapeSeries}
              />

            </div>
          </div>
        );
      }}
    </ScrapeContext.Consumer>
  );
}

function Latest({ list, scrapeSeries }) {
  let history = useHistory();
  const openSeries = (item) => {
    let url = "https://www.animekisa.cc/anime/" + fix(item.name).toLowerCase();
    scrapeSeries(url);
    history.push("/series");
  };

  if (list) {
    return (
      <Heading
        title="Recently Updated"
        margin="0rem 0rem 3rem 0rem"
        padding="0"
      >
        <div className="homeSeriesGrid">
          {list?.map(function (item) {
            return (
              <SeriesCard
                key={item.url}
                type="poster"
                data={item}
                onClick={() => {
                  openSeries(item);
                }}
              />
            );
          })}
        </div>
      </Heading>
    );
  } else {
    return <Loading></Loading>;
  }
}

function Recently({ list, scrapeSeries }) {
  let history = useHistory();
  const openSeries = (item) => {
    let url = "https://www.animekisa.cc/anime/" + fix(item.name).toLowerCase();
    scrapeSeries(url);
    history.push("/series");
  };

  if (list) {
    if (list.length === 0) {
      return <div></div>;
    }
    return (
      <Heading title="Recently Viewed" margin="0rem 0rem 3rem 0rem" padding="0">
        <div className="historyGrid">
          {list?.map(function (item) {
            return (
              <SeriesCard
                key={item.url}
                type="ticket"
                data={item}
                onClick={() => {
                  openSeries(item);
                }}
              />
            );
          })}
        </div>
      </Heading>
    );
  } else {
    return <Loading></Loading>;
  }
}

function fix(data) {
  const specials = [
    "☆",
    "!",
    "@",
    "#",
    "$",
    "%",
    "^",
    "&",
    "*",
    "(",
    ")",
    "_",
    " - ",
    " -",
    "- ",
    "+",
    "=",
    "[",
    "]",
    "{",
    "}",
    '"',
    ":",
    ";",
    ",",
    ".",
    "/",
    "?",
    ">",
    "<",
    "'",
    "|",
  ];
  let source = data.toString();
  for (let i = 0; i < specials.length; i++) {
    source = source.replaceAll(specials[i], "");
  }
  source = source.replaceAll(" ", "-");
  return source;
}

export default HomeScreen;
